import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Jira Project Management Dashboard Task';

  openListForm() {
    document.getElementById("listForm").style.display = "block";
  }

  openTaskForm() {
    document.getElementById("taskForm").style.display = "block";
  }
  

  // Getting form data and storing into array
  listNameArray:any=[];
  taskNameArray:any=[];
  ListName(name:string){
    this.listNameArray.push(name);
    document.getElementById("listForm").style.display = "none";
  }

  RemoveList(i:number){
    this.listNameArray.splice(i,1);
  }

  TaskName(details:any){
    this.taskNameArray.push(details);
    document.getElementById("taskForm").style.display = "none";
    
    
  }

  RemoveTask(n:number){
    this.taskNameArray.splice(n,1);
  }
  //drag and drop 

  
  
}
